<template>
  <el-main>
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item>
        <el-select class="selects" v-model="value" filterable clearable placeholder="请选择板块" @clear="handClear"  @change="handChange">
          <el-option
              v-for="item in options1"
              :key="item.value"
              :label="item.label"
              :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item >
        <el-input v-model="autho" placeholder="作者"></el-input>
      </el-form-item>

      <el-form-item >
        <el-input v-model="title" placeholder="标题"></el-input>
      </el-form-item>


      <el-form-item>
        <el-button type="primary" @click="onSubmit">查询</el-button>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSubmiterr">SQL注入查询</el-button>
        <el-button type="primary" icon="el-icon-question" @click="ondetail">缺陷演示实例</el-button>
      </el-form-item>


      <el-switch
          @change="selectOne"
          class="selectone"
          width="60"
          v-model="value1"
          active-color="#13ce66"
          inactive-color="#ff4949"
          active-text="按点击率排序">
      </el-switch>
    </el-form>

    <el-row :gutter="15">
      <el-col :span="8"  v-for="item in newsList" :key="item.ClassificationID" >
        <el-card class="card" :body-style="{ padding: '0px' }">
          <router-link :to="{name: 'newsdetail',query:{NewsID:item.NewsID}}"><img :src="item.NewsImages" class="imageNews"></router-link>
          <div style="padding: 14px;">
            <span class="newstitle">{{item.NewsTitle}}</span>
            <div class="from">
              <span class="autho">{{item.Author}}</span>
              <el-tag class="class">{{item.ClassificationName}}</el-tag>
            </div>

            <div class="bottom clearfix">
              <time class="time">{{ item.CreateTime }}</time>
              <el-button type="danger" round  class="button" size="small" >
                <router-link :to="{name: 'deleteNews',query:{NewsID:item.NewsID,ClassificationID:item.ClassificationID}}">删除</router-link>
              </el-button>
              <el-button type="primary"  round class="button" size="small">
                <router-link :to="{name: 'changeNews',query:{NewsID:item.NewsID}}">编辑</router-link>
              </el-button>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <div class="page">
      <el-pagination
          background="biue"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="pagesize"
          layout="total, prev, pager, next, jumper"
          :total="numNews">
      </el-pagination>

    </div>

  </el-main>
</template>

<script>
import {getAllnews} from "@/service/api";
import {searchree} from "@/service/api";
import {getAllClassification} from '@/service/api';
export default {
  data() {
    return {
      newsList:'',
      currentPage:1,
      numNews:'',
      pagesize:6,
      options1:[],
      value1:false,
      autho:'',
      title:''
    };
  },
  mounted(){

    sessionStorage.setItem("islogin",this.$route.query.islogin);
    let tempData=[];

    getAllnews(
        {

        }
    ).then((data)=>{
      if(data.code === 200){
        if(data.data.length<=6){
          for(var i=0;i<data.data.length;i++){

            if(data.data[i]){
              tempData.push(data.data[i]);



            }

          }

          this.numNews=data.data.length;
          this.newsList=tempData;
          console.log(this.newsList);


        }
        else if(data.data.length>6){
          for(var m=0;m<6;m++){

            if(data.data[m]){
              tempData.push(data.data[m]);

            }

          }

          this.numNews=data.data.length;
          this.newsList=tempData;
          console.log(this.newsList);
        }

        sessionStorage.setItem("news",JSON.stringify(data.data))
      }

    });

    // Store.commit('changeUserInfo',JSON.parse(sessionStorage.getItem("store")) );
    getAllClassification().then((data)=>{
      if(data.code===200){
        for(var i=0;i<data.data.length;i++){
          if(data.data[i]){
            if(data.data[i].classificationID===JSON.parse(sessionStorage.getItem("news"))[i].ClassificationID){
              this.options1.push({
                value: data.data[i].classificationID,
                label: data.data[i].classificationName
              })
            }

          }
        }
      }
    });
  },
  watch:{
    $route(to,from){
      console.log(to.path);
      console.log(from.path);

      window.location.reload();

    }


  },
  methods: {
    selectOne(val){
      let tempData=[];
      sessionStorage.removeItem('news');
      getAllnews(
          {
            IsOrder:val
          }
      ).then((data)=>{
        if(data.code === 200){
          if(data.data.length<=6){
            for(var i=0;i<data.data.length;i++){

              if(data.data[i]){
                tempData.push(data.data[i]);
              }

            }

            this.numNews=data.data.length;
            this.newsList=tempData;
            console.log(this.newsList);


          }
          else if(data.data.length>6){
            for(var m=0;m<6;m++){

              if(data.data[m]){
                tempData.push(data.data[m]);

              }

            }

            this.numNews=data.data.length;
            this.newsList=tempData;
            console.log(this.newsList);
          }

          sessionStorage.setItem("news",JSON.stringify(data.data))
        }
        if(data.code===400){
          this.$message({
            message: '无权获取!',
            type: 'error'
          });
        }

      });

    },
    ondetail(){
      this.$alert('<strong>此处标题搜索栏未对用户输入的查询条件做校验处理，<br>可在标题栏拼接恶意SQL语句实现查询<br>例如:输入  不存在此数据  点击查询结果为空<br>输入 不存在此数据"or"1"="1 点击SQL注入查询结果变成数据库中所有数据,造成数据泄露</strong>', 'SQL注入漏洞', {
        dangerouslyUseHTMLString: true,
        center: true,
        type: 'warning',
      });
    },
    handClear(){
      this.numNews=JSON.parse(sessionStorage.getItem("news")).length;
      this.newsList=JSON.parse(sessionStorage.getItem("news"));
    },
    handChange(val){
      this.$forceUpdate();
      let midlist=[];
      for(var o=0;o<JSON.parse(sessionStorage.getItem("news")).length;o++){
        if(JSON.parse(sessionStorage.getItem("news"))[o].ClassificationID===val){
          midlist.push(JSON.parse(sessionStorage.getItem("news"))[o]);
        }
        else if(JSON.parse(sessionStorage.getItem("news"))[o].NewsTitle===val){
          midlist.push(JSON.parse(sessionStorage.getItem("news"))[o]);
        }
        else if(JSON.parse(sessionStorage.getItem("news"))[o].Author===val){
          midlist.push(JSON.parse(sessionStorage.getItem("news"))[o]);
        }
      }
      console.log(midlist);
      this.newsList=midlist;
      this.numNews=midlist.length;

    },
    onSubmiterr() {
      let tempData=[];
      sessionStorage.removeItem('news');
      searchree(
          {
            NewsTitle:this.title,
            Author:this.autho,
            ClassificationsID:this.value
          }
      ).then((data)=>{
        if(data.code === 200){
          if(data.data){
            if(data.data.length<=6){
              for(var i=0;i<data.data.length;i++){

                if(data.data[i]){
                  tempData.push(data.data[i]);



                }

              }

              this.numNews=data.data.length;
              this.newsList=tempData;
              console.log(this.newsList);
              this.$notify({
                title: 'SQL注入漏洞警示',
                message: '标题搜索栏未对用户输入的搜索条件检验合法性,存在SQL注入漏洞。攻击者通过恶意拼接SQL语句便可获取到数据库信息从而造成数据泄露!',
                type: 'warning',
                duration: 0
              });


            }
            else if(data.data.length>6){
              for(var m=0;m<6;m++){

                if(data.data[m]){
                  tempData.push(data.data[m]);



                }

              }

              this.numNews=data.data.length;
              this.newsList=tempData;
              console.log(this.newsList);
              this.$notify({
                title: '警告',
                message: '标题搜索栏未对用户输入的搜索条件检验合法性,存在SQL注入漏洞。攻击者通过恶意拼接SQL语句便可获取到数据库信息从而造成数据泄露!',
                type: 'warning',
                duration: 0
              });
            }
            sessionStorage.setItem("news",JSON.stringify(data.data))
          }else if(!data.data){
            this.numNews=0;
            this.newsList={};
          }



        }
        if(data.code===400){
          this.$message({
            message: '无权获取!',
            type: 'error'
          });
        }

      });
    },
    onSubmit() {
      let tempData=[];
      sessionStorage.removeItem('news');
      getAllnews(
          {
            NewsTitle:this.title,
            Author:this.autho,
            ClassificationsID:this.value
          }
      ).then((data)=>{
        if(data.code === 200){
          if(data.data){
            if(data.data.length<=6){
              for(var i=0;i<data.data.length;i++){

                if(data.data[i]){
                  tempData.push(data.data[i]);



                }

              }

              this.numNews=data.data.length;
              this.newsList=tempData;
              console.log(this.newsList);
              this.$notify({
                title: 'SQL注入漏洞警示',
                message: '标题搜索栏未对用户输入的搜索条件检验合法性,存在SQL注入漏洞。攻击者通过恶意拼接SQL语句便可获取到数据库信息从而造成数据泄露!',
                type: 'warning',
                duration: 0
              });


            }
            else if(data.data.length>6){
              for(var m=0;m<6;m++){

                if(data.data[m]){
                  tempData.push(data.data[m]);



                }

              }

              this.numNews=data.data.length;
              this.newsList=tempData;
              console.log(this.newsList);
              this.$notify({
                title: '警告',
                message: '标题搜索栏未对用户输入的搜索条件检验合法性,存在SQL注入漏洞。攻击者通过恶意拼接SQL语句便可获取到数据库信息从而造成数据泄露!',
                type: 'warning',
                duration: 0
              });
            }
            sessionStorage.setItem("news",JSON.stringify(data.data))
          }else if(!data.data){
            this.numNews=0;
            this.newsList={};
          }



        }
        if(data.code===400){
          this.$message({
            message: '无权获取!',
            type: 'error'
          });
        }

      });
    },

    handleEdit() {
    },
    handleDelete() {
    },
    handleCurrentChange(val) {
      this.currentPage=val;
      getAllnews({

      }).then((data)=>{
        let tempData=[];
        for(var n=(this.currentPage-1)*6;n<this.currentPage*6;n++){
          if(data.data[n]){
            tempData.push(data.data[n])
          }
        }
        this.newsList=tempData;
        console.log(data.data.length)
      })
    }
  }
}
</script>

<style>
.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.imageNews {
  width: 100%;
  display: block;
  height: 100px;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both
}
.el-row{
  margin-bottom: 20px;
}
.autho{
  font-size: 13px;
  font-weight: bold;
  font-family: '宋体';
}

.from{
  margin-top: 5px;

}
.class{
  float: right;
  font-family: '宋体';
}
.page{
  text-align: center;
}
a{
  text-decoration: none;
  color: rgb(44, 40, 40);
}
.newstitle{
  font-family: '宋体';
  color:black;
  font-weight: bold;
  font-size: 18px;
}
.selects{
  /* margin: 5px; */
}
.selectone{
  float:right;
  font-weight: bold;
}
.card{
  height: 250px;

}
/* .el-icon-question{
  font-size: 40px;
  color: rgb(240, 82, 82);
} */

</style>





