<template>
  <el-main class="userMain">
    <div class="userTable">
      <el-table
          :data="tableData"
          style="width: 100%"
      >
        <el-table-column
            label="用户名"
            width="120">
          <template slot-scope="scope">{{ scope.row.UserName }}</template>
        </el-table-column>

        <el-table-column
            prop="Email"
            label="邮箱"
            width="300">
        </el-table-column>

        <el-table-column
            prop="Classifications"
            label="权限"
            show-overflow-tooltip>
        </el-table-column>

        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
                size="mini"
                @click="handleEdit(scope.$index, scope.row)">修改</el-button>
            <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="page">
      <el-pagination
          background="biue"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[3, 5, 10]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="numUsers">
      </el-pagination>

    </div>
  </el-main>
</template>

<script>
import { getAllUser } from "@/service/api";
import { deleteUser } from "@/service/api";
export default {
  components:{

  },
  data() {
    return {
      tableData: [
      ],
      currentPage:1,
      numUsers:'',
      pagesize:3

    }
  },
  watch:{
    $route(to,from){
      console.log(to.path);
      console.log(from.path);

      window.location.reload();

    }

  },
  mounted(){

    getAllUser().then((data)=>{
      let tempData=[];
      this.numUsers=data.data.length;
      console.log(data.data)
      for(var i=0;i<this.pagesize*this.currentPage;i++){

        if(data.data[i]){
          tempData.push(data.data[i])
        }

      }
      this.tableData=tempData;

    })
  },

  methods: {
    handleEdit(index) {
      console.log(this.tableData[index].UserName);
      this.$router.push({
        path:'/main/changeUser',
        query:{UserID:this.tableData[index].UserID}
      })

    },
    handleDelete(index) {
      console.log(this.tableData[index].UserID);

      deleteUser({
        UserID: this.tableData[index].UserID,
      }).then((data) => {
        if(data.code === 200) {  // 成功
          getAllUser().then((data)=>{
            let tempData=[];
            this.numUsers=data.data.length;
            for(var i=this.pagesize*(this.currentPage-1);i<this.pagesize*this.currentPage;i++){
              tempData.push(data.data[i])
            }
            this.tableData=tempData;

          })
        }   else {
          alert('error!!');
          return false;
        }
      })

    },
    handleSizeChange(val) {
      this.pagesize=val;

      getAllUser().then((data)=>{
        let tempData=[];
        this.numUsers=data.data.length;
        for(var i=this.pagesize*(this.currentPage-1);i<this.pagesize*this.currentPage;i++){
          if(data.data[i]){
            tempData.push(data.data[i])
          }
        }
        this.tableData=tempData;

      })
    },
    handleCurrentChange(val) {
      this.currentPage=val;
      getAllUser().then((data)=>{
        let tempData=[];
        this.numUsers=data.data.length;
        for(var i=this.pagesize*(this.currentPage-1);i<this.pagesize*this.currentPage;i++){
          if(data.data[i]){
            tempData.push(data.data[i])
          }

        }
        this.tableData=tempData;

      })

    }
  }
}
</script>

<style scoped>
.page{
  text-align: center;
}
.userTable{
  height: 380px;
}
.userMain{
  height:470px ;
  font-family: '宋体';
}

</style>