<template>
  <div class="change-section">
    <el-form
        label-position="left"
        :rules="rules"
        :model="ruleForm" status-icon
        ref="ruleForm" label-width="100px" class="demo-ruleForm"
    >
      <!-- <el-form-item label="新闻ID" prop="id">
        <el-input type="text" v-model="ruleForm.id" autocomplete="off"></el-input>
      </el-form-item> -->
      <!-- <el-form-item label="所属分类" prop="name">
        <el-input type="text" v-model="ruleForm.name" autocomplete="off"></el-input>
      </el-form-item> -->
      <el-form-item label="所属分类" prop="ClassificationsID">
        <el-select v-model="value"  placeholder="请选择">
          <el-option
              v-for="item in cities"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            <span style="float: left">{{ item.label }}</span>
            <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="新闻标题" prop="title">
        <el-input type="text" v-model="ruleForm.title" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="作者" prop="autho">
        <el-input type="text" v-model="ruleForm.autho" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="新闻内容" prop="message">
        <el-input type="textarea" v-model="ruleForm.message" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="备注" prop="others">
        <el-input type="textarea" v-model="ruleForm.others" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="点击量" prop="clicks">
        <el-input type="Number" v-model="ruleForm.clicks" autocomplete="off"></el-input>
      </el-form-item>


      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import {changeNews} from '@/service/api';
import {getAllClassification} from '@/service/api';
export default {

  data() {
    return {
      ruleForm: {
        // id:this.$route.query.NewsID,
        name:this.$route.query.ClassificationID,
        title:'',
        autho:'',
        message:'',
        others:'',
        clicks:'',
        // title:this.$route.query.NewsTitle,
        // autho:this.$route.query.Author,
        // message:this.$route.query.Content,
        // others:this.$route.query.OtherInfo,
        // clicks:this.$route.query.ClickQuantity,
      },
      cities:[],
      rules: {
        name: [
          { required: true, message: '请输入名称', trigger: 'blur' }
        ],
      },
      value: this.$route.query.ClassificationID
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          changeNews({
            NewsID:this.$route.query.NewsID,
            ClassificationID:this.ruleForm.name,
            NewsTitle:this.ruleForm.title,
            Author:this.ruleForm.autho,
            Content:this.ruleForm.message,
            OtherInfo:this.ruleForm.others,
            ClickQuantity:parseInt(this.ruleForm.clicks)
          }).then((data) => {
            if(data.code === 200) {  // 成功             
              this.$message({
                message: '修改成功!',
                type: 'success'
              });
              this.$router.push({
                path:'/main/addNews',
              })
            }
          })
        } else {
          alert('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
  mounted(){

    //  this.numNews=JSON.parse(sessionStorage.getItem("news")).length;
    //    this.newsList=JSON.parse(sessionStorage.getItem("news"));

    let allNews=JSON.parse(sessionStorage.getItem("news")).length;
    let newslist=JSON.parse(sessionStorage.getItem("news"));

    for(var p=0;p<allNews;p++){
      if(newslist[p].NewsID===this.$route.query.NewsID){
        this.ruleForm.title=newslist[p].NewsTitle;
        this.ruleForm.autho=newslist[p].Author;
        this.ruleForm.message=newslist[p].Content;
        this.ruleForm.others=newslist[p].OtherInfo;
        this.ruleForm.clicks=newslist[p].ClickQuantity;

      }
    }

    getAllClassification().then((data)=>{
      if(data.code===200){
        for(var i=0;i<data.data.length;i++){
          if(data.data[i]){
            if(data.data[i].classificationID!=1){
              this.cities.push({
                value: data.data[i].classificationID,
                label: data.data[i].classificationName
              })
            }

          }
        }
      }
    })
  }
}
</script>

<style lang="stylus">
.change-section
  padding 0px 20px
</style>
