<template>
  <div class="login-section">
    <el-form
        label-position="top"
        :rules="rules"
        :model="ruleForm" status-icon
        ref="ruleForm" label-width="100px" class="demo-ruleForm"
    >
      <el-form-item label="用户名" prop="name">
        <el-input type="text" v-model="ruleForm.name" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="Password">
        <el-input type="password" v-model="ruleForm.Password" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="邮箱" prop="Email">
        <el-input type="text" v-model="ruleForm.Email" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="备注" prop="others">
        <el-input type="text" v-model="ruleForm.others" autocomplete="off"></el-input>
      </el-form-item>

      <el-form-item label="负责板块" prop="ClassificationsID">
        <el-select v-model="value" multiple placeholder="请选择">
          <el-option
              v-for="item in cities"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            <span style="float: left">{{ item.label }}</span>
            <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
          </el-option>
        </el-select>



      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import {register} from '@/service/api';

import {getAllClassification} from '@/service/api';
export default {

  data() {
    return {
      ruleForm: {
        name:'',
        Email: '',
        Password: '',
        others:'',

      },
      rules: {
        Email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
        ],
        Password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '请输入名称', trigger: 'blur' }
        ],
        others: [
          { required: true, message: '请输入备注信息', trigger: 'blur' }
        ],
      },
      cities: [],
      value: ''
    };
  },
  methods: {

    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          register({
            Email: this.ruleForm.Email,
            Password:this.ruleForm.Password,
            UserName:this.ruleForm.name,
            ClassificationsID:this.value,
            OtherInfo:this.ruleForm.others
          }).then((data) => {
            if(data.code === 200) {  // 成功

              this.$message({
                message: '注册成功!',
                type: 'success'
              });
              this.$router.push({
                path:'/main/addnews',
              })
            }
          })
        } else {
          alert('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },


  },
  mounted(){
    getAllClassification().then((data)=>{
      if(data.code===200){
        for(var i=0;i<data.data.length;i++){
          if(data.data[i]){
            this.cities.push({
              value: data.data[i].classificationID,
              label: data.data[i].classificationName
            })
          }
        }
      }
    })
  }
}
</script>

<style lang="stylus">
.login-section
  padding 0px 20px
</style>
