<template>
  <el-main>
    <div class="tops">
      <i class="el-icon-warning"></i>被攻击后的状态 </div>
    <!-- <el-input  type="text" v-model="value" placeholder="请输入分类ID" ></el-input> -->
    <el-select class="valueid" v-model="value"  placeholder="请选择分类ID">
      <el-option
          v-for="item in cities"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        <span style="float: left">{{ item.label }}</span>
        <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
      </el-option>
    </el-select>
    <el-button class="xssbut" @click="handelxss" type="danger">XSS漏洞攻击</el-button>
    <el-button class="xssbuts" @click="handelelxss" type="danger">错误查看</el-button>
    <el-button class="xssbuts" @click="handelelelxss" type="primary">正确查看</el-button>


    <div class="xss0">
      <table class="tables" border='1' width="100%" >
        <tr align="center">
          <td>
            XSS漏洞处理后添加的正确数据
          </td>
          <td>
            分类名
          </td>
          <td>
            分类ID
          </td>
          <td>
            备注信息
          </td>

        </tr>
        <tr align="center">
          <td>
            XSS漏洞处理后添加的正确数据
          </td>
          <td>
            {{XSS0[0].ClassificationName}}
          </td>
          <td>
            {{XSS0[0].ClassificationID}}
          </td>
          <td>
            {{XSS0[0].OtherInfo}}
          </td>

        </tr>
      </table>

    </div>

    <div class="xss1" v-html="XSS1">

    </div>

    <div class="detailsss">
      <el-collapse accordion>
        <el-collapse-item>
          <template slot="title">
            漏洞描述<i class="header-icon el-icon-question"></i>
          </template>
          <div class="details1">
            <p>跨站脚本攻击（Cross-Site Scripting，XSS）是指通过存在安全漏洞的Web 网站注册用户的浏览器内运行非法的 HTML 标签或 JavaScript 进行的一种攻击。XSS漏洞经常出现在需要用户输入的地方，这些地方一旦对输入不进行处理，黑客就可以进行HTML注入，进而篡改网页。</p>
            <p>常见形式 1.插入恶意JS脚本 ( 简单举例:  &#60;img src="http:www.xxx.com" onerror="alert('xss攻击')&#62;   )</p>
            <p>2.插入恶意html代码 ( 简单举例:  &#60;s&#62;123&#60;/s&#62;  )</p>
            <p>3.插入伪造的图片或非法网站等 ( 简单举例:  &#60;img  src="https://img1.baidu.com/it/u=3140958831,606641020&fm=26&fmt=auto&gp=0.jpg"&#62; )  </p>
          </div>
        </el-collapse-item>
        <el-collapse-item>
          <template slot="title">
            漏洞危害<i class="header-icon el-icon-error"></i>
          </template>
          <div class="details1">
            <p>钓鱼网站，高级钓鱼攻击。</p>
            <p>删除目标文章、恶意篡改数据、嫁祸。</p>
            <p>劫持用户Web行为，甚至进一步渗透内网。</p>
            <p>蠕虫式挂马攻击、刷广告、刷浏量、破坏网上数据。</p>
            <p>其他安全问题。</p>
          </div>
        </el-collapse-item>
        <el-collapse-item>
          <template slot="title">
            防御手段<i class="header-icon el-icon-s-cooperation"></i>
          </template>
          <div class="details1">
            <p>永远不要相信用户的输入，也永远不要相信服务器返回的数据。将用户所提供的内容进行过滤。</p>
            <p>1、对于输入：检测是否有script.img.等标签2、对于输出：对于尖括号等特殊字符必须做转义和反转义处理;</p>
          </div>

        </el-collapse-item>

      </el-collapse>
    </div>

  </el-main>
</template>

<script>
import { getClassification } from "@/service/api";
import {getAllClassification} from '@/service/api';
// 获取到那一条数据  对传入的数据不做任何处理 原封不动返回字符串
export default {
  data(){
    return{
      XSS1:'',
      value:'',
      XSS0:[{
        ClassificationName:'',
        ClassificationID:'',
        OtherInfo:''
      }],
      cities:[],
    }
  },
  methods:{
    handelxss(){
      this.$router.push({
        path:'/main/xss1',
      })
    },
    handelelelxss(){
      getClassification(
          {
            ClassificationID:this.value
          }
      ).then((data)=>{
        if(data.code===200){
          this.XSS0[0].ClassificationName=data.data.ClassificationName,
              this.XSS0[0].ClassificationID=data.data.ClassificationID,
              this.XSS0[0].OtherInfo=data.data.OtherInfo

        }
      })
    },
    handelelxss(){
      console.log(this.value);
      getClassification(
          {
            ClassificationID:this.value
          }
      ).then((data)=>{
        let html='';
        if(data.code===200){
          html=`<table class="tables" border='1' width="100%" >
                            <tr align="center"> 
                                <td>
                                XSS漏洞未处理添加的错误数据
                                </td>
                                <td>
                                分类名
                                </td>
                                <td>
                               分类ID
                                </td>
                                <td>
                               备注信息
                                </td>                              
                            </tr>
                            <tr align="center"> 
                                <td>
                                XSS漏洞未处理添加的错误数据
                                </td>
                                <td>
                                ${data.data.ClassificationName}
                                </td>
                                <td>
                                ${data.data.ClassificationID}
                                </td>
                                <td>
                                ${data.data.OtherInfo}
                                </td>                               
                            </tr>
                        </table>`
          this.XSS1=html;
        }
      })
    }
  },
  mounted(){
    getAllClassification().then((data)=>{
      if(data.code===200){
        for(var i=0;i<data.data.length;i++){
          if(data.data[i]){
            if(data.data[i].classificationID!=1){
              this.cities.push({
                value: data.data[i].classificationID,
                label: data.data[i].classificationName
              })
            }

          }
        }
      }
    })
  }
}
</script>
<style scoped>
.xss0{
  /* border: 1px solid; */
  width: 100%;
  /* height: 100px; */
  background-color:white;
  margin-bottom: 20px;
}
.xss1{
  /* border: 1px solid; */
  width: 100%;
  /* height: 100px; */
  background-color:white;
}
.el-icon-warning{
  color: red;
  font-size: 50px;
}
.tops{
  font-size: 50px;

}
.detailsss{
  /* border: 1px solid; */
  margin-top: 10px;
  width: 100%;
  height: auto;
  /* font-family: '宋体';
  font-size: 50px; */
}
.details1{
  text-indent: 20px;
  font-family: '宋体';
  font-size: 20px;
}
.tables{
  width: 100%;
  height: 51px;
}
.xssbut{
  margin-bottom: 20px;
}
.valueid{
  margin-right: 10px;
}
</style>