<template>
  <div class="delete-section">
    <el-form
        label-position="top"
        :rules="rules"
        :model="ruleForm" status-icon
        ref="ruleForm" label-width="100px" class="demo-ruleForm"
    >
      <el-form-item label="新闻ID" prop="id">
        <el-input type="text" v-model="ruleForm.id" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="所属分类" prop="name">
        <el-input type="text" v-model="ruleForm.name" autocomplete="off"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">删除</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import {deleteNews} from '@/service/api';
export default {

  data() {
    return {
      ruleForm: {
        id:this.$route.query.NewsID,
        name:this.$route.query.ClassificationID,

      },
      rules: {
        id: [
          { required: true, message: '请输入用户ID', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '请输入名称', trigger: 'blur' }
        ],
      },
      value: ''
    };
  },
  methods: {

    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          deleteNews({
            NewsID:this.ruleForm.id,
            ClassificationID:this.ruleForm.name,
          }).then((data) => {
            if(data.code === 200) {  // 成功             
              this.$message({
                message: '删除成功!',
                type: 'success'
              });
              this.$router.push({
                path:'/main/addNews',
              })
            }
          })
        } else {
          alert('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  }
}
</script>

<style lang="stylus">
.delete-section
  padding 0px 20px
</style>
