<template>
  <div class="newsdetail">
    <div class="title">
      {{Title}}
    </div>
    <div>
      <el-tag class="autho">{{Autho}}</el-tag>
    </div>
    <div class="image">
      <img :src="Image" class="images">
    </div>

    <div class="suggest">
      新闻详情
    </div>

    <div class="contentword">
      <p class="content">
        {{Content}}
      </p>
    </div>

    <div class="bottom">
      <span class="time">{{Time}}</span>
      <el-tooltip content="新闻浏览量" placement="top" effect="light">
        <i class="el-icon-view">{{Click}}</i>
      </el-tooltip>

    </div>

  </div>
</template>

<script>
import {getNews} from '@/service/api';
import {addClick} from '@/service/api';
export default {
  data(){
    return{
      Title:'',
      Image:'',
      Autho:'',
      Content:'',
      Time:'',
      Click:''

    }
  },
  mounted(){
    getNews(
        {
          NewsID:this.$route.query.NewsID
        }
    ).then((data)=>{
      if(data.code === 200){
        this.Title=data.data.NewsTitle;
        this.Image=data.data.NewsImage;
        this.Autho=data.data.Author;
        this.Content=data.data.Content;
        this.Time=data.data.UpdateTime;
        this.Click=data.data.ClickQuantity
      }
    }),
        addClick({
          NewsID:this.$route.query.NewsID
        }).then((data)=>{
          if(data.code===200){
            return
          }
        })

  }

}
</script>

<style lang="stylus" scoped>
.newsdetail
  width 700px
  height 550px
  background-color #fff
  margin 0 auto
  box-shadow: 0 0 25px #cac6c6;
  border-radius: 10px;
  padding 20px

.title
  text-align center
  font-family '宋体'
  font-size 28px
  font-weight bold
  height 30px

.autho
  float right
  margin-top 40px
  font-size 25px
  font-family '宋体'


.content
  margin-left 30px

.suggest
  font-weight bold

.bottom
  border-top 1px solid
  border-top-style dashed
  padding 20px


.time
  color #999
  font-size 14px
  float right
i
  color blue
.image{

  height 275px
}
.images{
  height 200px

  width 100%
}

.content{
  font-family '宋体'

}
.contentword{
  padding-top 0px
  height 180px

  overflow auto
}
</style>