<template>
  <el-main class="classMain">
    <div class="classTable">
      <el-table
          :data="tableData"
          style="width: 100%"
      >
        <el-table-column
            label="分类名"
            width="120">
          <template slot-scope="scope">{{ scope.row.classificationName }}</template>
        </el-table-column>

        <el-table-column
            prop="classificationID"
            label="分类编号"
            width="100">
        </el-table-column>

        <el-table-column
            prop="updateUserId"
            label="更新者"
            width="100">
        </el-table-column>

        <el-table-column
            prop="createTime"
            label="创建时间"
            width="300">
        </el-table-column>

        <el-table-column
            prop="updateTime"
            label="更新时间"
            show-overflow-tooltip>
        </el-table-column>

        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
                size="mini"
                @click="handleEdit(scope.$index, scope.row)">修改</el-button>
            <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="page">
      <el-pagination
          background="biue"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[3,5, 10]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="numClass">
      </el-pagination>

    </div>
  </el-main>
</template>

<script>
import { getAllClassification } from "@/service/api";
import {deleteClass} from "@/service/api";


export default {
  components:{

  },
  data() {
    return {
      tableData: [
      ],
      currentPage:1,
      numClass:'',
      pagesize:3

    }
  },
  mounted(){

    getAllClassification().then((data)=>{
      let tempData=[];
      this.numClass=data.data.length;
      console.log(data.data)
      for(var i=0;i<this.pagesize*this.currentPage;i++){

        if(data.data[i]){
          data.data[i].createTime=data.data[i].createTime.replace(/T/,' ');
          data.data[i].createTime=data.data[i].createTime.replace(/\+.*/,' ');
          data.data[i].updateTime=data.data[i].updateTime.replace(/T/,' ');
          data.data[i].updateTime=data.data[i].updateTime.replace(/\+.*/,' ');
          tempData.push(data.data[i])
        }

      }
      this.tableData=tempData;

    })
  },

  methods: {
    handleEdit(index) {
      console.log(this.tableData[index].classificationName);
      this.$router.push({
        path:'/main/changeClass',
        query:{ClassificationID:this.tableData[index].classificationID}
      })
    },

    handleDelete(index) {
      console.log(this.tableData[index].classificationID);
      deleteClass({
        ClassificationID: this.tableData[index].classificationID,
      }).then((data) => {
        if(data.code === 200) {  // 成功
          getAllClassification().then((data)=>{
            let tempData=[];
            this.numClass=data.data.length;
            for(var i=this.pagesize*(this.currentPage-1);i<this.pagesize*this.currentPage;i++){

              if(data.data[i]){
                data.data[i].createTime=data.data[i].createTime.replace(/T/,' ');
                data.data[i].createTime=data.data[i].createTime.replace(/\+.*/,' ');
                data.data[i].updateTime=data.data[i].updateTime.replace(/T/,' ');
                data.data[i].updateTime=data.data[i].updateTime.replace(/\+.*/,' ');
                tempData.push(data.data[i])
              }

            }
            this.tableData=tempData;

          })
        }   else {
          alert('error!!');
          return false;
        }
      })

    },
    handleSizeChange(val) {
      this.pagesize=val;

      getAllClassification().then((data)=>{
        let tempData=[];
        this.numClass=data.data.length;
        for(var i=this.pagesize*(this.currentPage-1);i<this.pagesize*this.currentPage;i++){
          if(data.data[i]){
            data.data[i].createTime=data.data[i].createTime.replace(/T/,' ');
            data.data[i].createTime=data.data[i].createTime.replace(/\+.*/,' ');
            data.data[i].updateTime=data.data[i].updateTime.replace(/T/,' ');
            data.data[i].updateTime=data.data[i].updateTime.replace(/\+.*/,' ');
            tempData.push(data.data[i])
          }

        }
        this.tableData=tempData;

      })
    },
    handleCurrentChange(val) {
      this.currentPage=val;
      getAllClassification().then((data)=>{
        let tempData=[];
        this.numClass=data.data.length;
        for(var i=this.pagesize*(this.currentPage-1);i<this.pagesize*this.currentPage;i++){
          if(data.data[i]){
            data.data[i].createTime=data.data[i].createTime.replace(/T/,' ');
            data.data[i].createTime=data.data[i].createTime.replace(/\+.*/,' ');
            data.data[i].updateTime=data.data[i].updateTime.replace(/T/,' ');
            data.data[i].updateTime=data.data[i].updateTime.replace(/\+.*/,' ');
            tempData.push(data.data[i])
          }

        }
        this.tableData=tempData;

      })

    }
  }
}
</script>

<style scoped>
.page{
  text-align: center;
}
.classTable{
  height: 380px;
}
.classMain{
  height:470px ;
  font-family: '宋体';
}

</style>