<template>
  <el-main>
    <div class="ranking">
      <el-carousel :interval="4000" type="card" height="380px">
        <el-carousel-item v-for="(item,index) in clickData" :key="index">
          <div class="image">
            <router-link :to="{name: 'newsdetail',query:{NewsID:item.NewsID}}"><img :src="item.NewsImages" class="images"></router-link>
          </div>
          <div class="tips">
            <img class="imgs"
                 src="../../img/i5.jpg">
            <span class="top">Top{{index+1}}</span>
            <p class="num">浏览量{{item.ClickQuantity}}</p>
          </div>
        </el-carousel-item>
      </el-carousel>
    </div>


    <div class="bz">
      <span class="popular">最受欢迎新闻</span>
    </div>
  </el-main>
</template>

<script>
import {getAllnews} from "@/service/api";
export default {
  data(){
    return{
      clickData:[]
    }
  },
  watch:{
    $route(to,from){
      console.log(to.path);
      console.log(from.path);

      window.location.reload();

    }


  },
  methods:{
    handelchange(){
      console.log(111)
    }
  },
  mounted(){
    let tempData=[];
    let testclass=[];
    testclass=JSON.parse(sessionStorage.getItem("store")).ClassificationsID;
    getAllnews({
          ClassificationsID:testclass
        }

    ).then((data)=>{
      if(data.code===200){
        if(data.data.length>5){
          for(var i=0;i<data.data.length;i++){
            if(data.data[i]){
              tempData.push(data.data[i]);
            }
          }
          this.clickData=tempData.sort(function(a,b){
            return b.ClickQuantity - a.ClickQuantity;
          }).slice(0,5)
        }

        else if(data.data.length<5){
          for(var m=0;m<data.data.length;m++){
            if(data.data[m]){
              tempData.push(data.data[m]);
            }
          }
          this.clickData=tempData.sort(function(a,b){
            return b.ClickQuantity - a.ClickQuantity;
          }).slice(0,data.data.length)
        }


      }
    })
  }

}
</script>

<style  scoped>
.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 200px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #b8cae4;
}

.el-carousel__item:nth-child(2n+1) {
  background-color: #98b4d3;
}

.bz{
  text-align: center;
}
.popular{
  font-family: '宋体';
  font-size: 30px;
  font-weight: bold;
}

.image{
  width: 450px;
  height: 375px;
  float: left;
  margin: 0px;
}

.images{
  width: 450px;
  height: 379px;
}
.num{
  font-family: '宋体';
}
.top{
  font-size: 40px;
  font-style: oblique;
  font-weight: bold;
  color: rgb(255, 7, 7);
  font-family: '宋体';

}

.tips{
  margin-left: 0px;
  float: right;
  margin-top: 1px;
  text-align: center;
  height: 225px;
  width: 180px;

  /* padding-top: 150px; */
}
.imgs{
  width: 100%;
  height: 100%;




}




</style>