import Vue from 'vue'
import VueRouter from 'vue-router'


import Store from '@/store'
import Main from "../components/main.vue";
import Login from "../views/user-login/index.vue";
import Register from "../views/user-login/reindex.vue";
import Edit from "../views/user-login/edindex.vue";
import What from "../components/what.vue";
import AllUsers from "../views/peoples/allUsers.vue";
import Change from "../views/user-login/chindex.vue";
import AllClass from "../views/classifications/allClass.vue";
import AddClass from "../views/classifications/addindex.vue";
import ChangeClass from "../views/classifications/chindex.vue";
import ChangeUser from "../views/peoples/chindex.vue";
import Addnews from "../views/news/addNews.vue";
import DeleteNews from "../views/news/deindex.vue";
import ChangeNews from "../views/news/chindex.vue";
import NewsDetail from "../views/news/detail.vue";
import News from "../views/news/adindex.vue";
import Rank from "../views/rank/newsRank.vue";
// import Xss from "../views/ceshi/test.vue";
import xss1 from "../views/classifications/addxssindex.vue";
import Xss1 from "../views/classifications/xss1.vue";



import {nowuserInfo} from "../service/api";
Vue.use(VueRouter)



const router = new VueRouter({
  mode: 'history',
  base: '',
  routes:[
    {
      path:'/',
      name:'login',
      title:'登录',
      component:Login
    },
    {
      path:'/edit',
      name:'edit',
      title:'修改个人资料',
      component:Edit
    },
    {
      path:'/change',
      name:'change',
      title:'修改个人密码',
      component:Change
    },
    {
      path: '/main',
      name: 'main',
      title: '首页',
      component: Main,
      children: [
        {
          path: 'addnews',
          name: 'addNews',
          title: '添加新闻',
          component:What ,  
        },
        {
          path: 'allUsers',
          name: 'allUsers',
          title: '显示所有用户信息',
          component:AllUsers ,  
        },
        {
          path:'/register',
          name:'register',
          title:'注册',
          component:Register
        },
        {
          path: 'allClass',
          name: 'allClass',
          title: '显示所有分类信息',
          component:AllClass ,  
        },
        {
          path: 'addClass',
          name: 'addClass',
          title: '新增分类信息',
          component:AddClass ,  
        },
        {
          path: 'changeClass',
          name: 'changeClass',
          title: '修改分类信息',
          component:ChangeClass ,  
        },
        {
          path: 'changeUser',
          name: 'changeUser',
          title: '修改用户信息',
          component:ChangeUser ,  
        },
        {
          path: 'test',
          name: 'test',
          title: '测试',
          component:Addnews ,  
        },
        {
          path: 'deleteNews',
          name: 'deleteNews',
          title: '删除新闻',
          component:DeleteNews ,  
        },
        {
          path: 'changeNews',
          name: 'changeNews',
          title: '修改新闻',
          component:ChangeNews ,  
        },
        {
          path: 'newsdetail',
          name: 'newsdetail',
          title: '新闻详情',
          component:NewsDetail ,  
        },
        {
          path: 'news',
          name: 'news',
          title: '添加新闻',
          component:News ,  
        },
        {
          path: 'rank',
          name: 'rank',
          title: '排行榜',
          component:Rank ,  
        },
        {
          path: 'xss',
          name: 'xss',
          title: 'xss攻击',
          component:Xss1 ,  
        },
        {
          path: 'xss1',
          name: 'xss1',
          title: 'xss攻击1',
          component:xss1,  
        },
      ]
    }
  ]
})


router.beforeEach(async (to, from, next) => {
  // 进入路由的时候，都要想后端发送token,验证合法不合法
  // 不管路由需要不需要登录，都需要展示用户信息
  // 都需要想后端发请求，拿到用户信息
  const data = await nowuserInfo();
  Store.commit('changeUserInfo', data.data);
  next();

  
})

export default router
