<template>
  <div class="addNews-section">
    <el-form
        label-position="left"
        :rules="rules"
        :model="ruleForm" status-icon
        ref="ruleForm" label-width="100px" class="demo-ruleForm"
    >
      <!-- <el-form-item label="所属分类" prop="name">
        <el-input type="text" v-model="ruleForm.name" autocomplete="off"></el-input>
      </el-form-item> -->
      <el-form-item label="所属分类" prop="ClassificationsID">
        <el-select v-model="value"  placeholder="请选择">
          <el-option
              v-for="item in cities"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            <span style="float: left">{{ item.label }}</span>
            <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="新闻标题" prop="title">
        <el-input type="text" v-model="ruleForm.title" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="作者" prop="autho">
        <el-input type="text" v-model="ruleForm.autho" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="新闻内容" prop="message">
        <el-input type="textarea" v-model="ruleForm.message" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="备注" prop="others">
        <el-input type="textarea" v-model="ruleForm.others" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="正常上传图片" prop="NewsImage">
        <!-- accept="image/gif,image/jpeg,image/jpg,image/png" -->
        <input @change="handupload" id="uploadimg" type="file" accept="image/gif,image/jpeg,image/jpg,image/png"/>
      </el-form-item>
      <el-form-item label="存在上传漏洞" prop="NewsImage">
        <!-- accept="image/gif,image/jpeg,image/jpg,image/png" -->
        <input @change="handuploads" id="uploadimg" type="file"/>
      </el-form-item>
      <div class="upimg">
        <img class="upimgs" :src="imgurl" />
      </div>

      <el-form-item>

        <!-- <el-button type="primary" @click="uploads">上传</el-button> -->
        <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import {addNews} from '@/service/api';
import {upload} from '@/service/api';
import {getAllClassification} from '@/service/api';
var tokens=localStorage.getItem('token');
console.log(tokens);

export default {
  data() {
    return {
      ruleForm: {
        title:'',
        others:'',
        message:'',
        autho:'',
        NewsImage:''

      },
      cities:[],
      mytoken:{token:tokens},
      imgurl: '',
      filedata:'',
      rules: {
        name: [
          { required: true, message: '请输入名称', trigger: 'blur' }
        ],
      },
      value: ''
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          addNews({
            NewsImage:this.filedata,
            ClassificationID:this.value,
            NewsTitle:this.ruleForm.title,
            Author:this.ruleForm.autho,
            Content:this.ruleForm.message,
            OtherInfo:this.ruleForm.others,
          }).then((data) => {
            if(data.code === 200) {  // 成功             
              alert("添加成功");
              this.$router.push({
                path:'/main/addNews',
              })
            }
          })
        } else {
          alert('error submit!!');
          return false;
        }
      });
    },
    handupload(e){
      var avatarImg = e.target.files[0];
      var avatarData = new FormData();
      avatarData.append('file', avatarImg);
      avatarData.append('dirPath',"images/news");
      var testurl=window.URL.createObjectURL(avatarImg);
      console.log(testurl);
      this.imgurl=testurl;
      upload(
          avatarData,
      ).then((data)=>{
        if(data.code===200){
          //  this.$message({
          //        message: '图片上传成功!',
          //        type: 'success'
          //       });
          this.filedata=data.data;
          console.log(this.filedata);

          this.$alert('<strong>此处上传对用户提交的文件做了校验处理，限制了可以上传的类型，保证用户提交的文件合理合法</strong>', '图片上传成功', {
            dangerouslyUseHTMLString: true,
            center: true,
            type: 'success',
          });



        }

      })
    },
    handuploads(e){
      var avatarImg = e.target.files[0];
      var avatarData = new FormData();
      avatarData.append('file', avatarImg);
      avatarData.append('dirPath',"images/news");
      var testurl=window.URL.createObjectURL(avatarImg);
      console.log(testurl);
      this.imgurl=testurl;
      upload(
          avatarData,
      ).then((data)=>{
        if(data.code===200){
          //  this.$message({
          //        message: '恶意攻击代码已经上传成功!',
          //        type: 'error'
          //       });
          this.filedata=data.data;
          console.log(this.filedata);
          this.$alert('<strong>此处上传对用户提交的文件没做任何校验处理，用户提交的任何文件都会被上传至服务器，潜在危害极大，如果用户提交了木马文件等恶意代码，网站的安全性会受到很大威胁!</strong>', '存在上传漏洞,恶意攻击文件已上传成功', {
            dangerouslyUseHTMLString: true,
            center: true,
            type: 'warning',
          });

        }

      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
  mounted(){
    getAllClassification().then((data)=>{
      if(data.code===200){
        for(var i=0;i<data.data.length;i++){
          if(data.data[i]){
            if(data.data[i].classificationID!=1){
              this.cities.push({
                value: data.data[i].classificationID,
                label: data.data[i].classificationName
              })
            }

          }
        }
      }
    })
  }
}
</script>

<style lang="stylus">
.addNews-section
  padding 0px 20px

.upimg{
  border solid 1px
  height 100px
  width 100px
  float left

}

.upimgs{
  height 100%
  width 100%
}

</style>
