<template>
  <div class="login-section">
    <el-form
        label-position="top"
        :rules="rules"
        :model="ruleForm" status-icon
        ref="ruleForm" label-width="100px" class="demo-ruleForm"
    >
      <el-form-item label="邮箱" prop="Email">
        <el-input type="text" v-model="ruleForm.Email" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="Password">
        <el-input type="password" v-model="ruleForm.Password" autocomplete="off"></el-input>
      </el-form-item>

      <el-form-item label="验证码" prop="VerifyCode">

        <el-input type="password" v-model="ruleForm.VerifyCode" autocomplete="off">
        </el-input>

      </el-form-item>
      <el-form-item>
        <el-button type="info" icon="el-icon-message" circle @click="getVerify"></el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import {login} from '@/service/api';
import {getVerifyCode} from '@/service/api';
import  {encryptedData} from "@/mima/index.js";
// import Store from '@/store'
export default {
  data() {
    return {
      ruleForm: {
        Email: '',
        password: '',
        VerifyCode:''
      },
      watch:{
        $route(to,from){
          console.log(to.path);
          console.log(from.path);

          window.location.reload();

        }


      },
      rules: {
        Email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
        ],
        Password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ],
        VerifyCode: [
          { required: true, message: '请输入验证码', trigger: 'blur' }
        ],
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          login({
            Email: this.ruleForm.Email,
            Password: encryptedData(this.ruleForm.Password),
            // Password: this.ruleForm.Password,
            VerifyCode:this.ruleForm.VerifyCode
          }).then((data) => {
            if(data.code === 200) {  // 成功
              localStorage.setItem('token', data.data.token);
              this.$message({
                message: '登录成功!',
                type: 'success'
              });
              // Store.commit('changeUserInfo', data.data.user);
              sessionStorage.setItem("store",JSON.stringify(data.data.user))
              console.log( data.data.user);
              console.log(this.$store.state.userInfo);
              this.$router.push({
                path:'/main/addnews',
                query:{
                  islogin:1
                }
              })
            } else{
              this.$message({
                message: '登录失败,请重新登录',
                type: 'error'
              });
            }
          })
        } else {
          alert('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    getVerify(){
      getVerifyCode({
        Email: this.ruleForm.Email,
      }).then((data)=>{
        if(data.code === 200) {  // 成功
          alert("成功")
        }
      })
    }

  }
}
</script>

<style lang="stylus">
.login-section
  padding 0px 20px
</style>
