<template>
  <el-header style="text-align:center;height:100px; font-size: 12px;margin:10px 10px">

    <span class="MainTitle">缺陷演示系统</span>


    <span  class="username">{{t}}</span>
    <el-dropdown  @command="handleCommand">

      <i class="el-icon-edit" style="margin-right: 15px; font-size: 20px"></i>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item command='edit'>查看资料</el-dropdown-item>
        <el-dropdown-item command='exit'>注销账号</el-dropdown-item>
        <el-dropdown-item command='change'>修改密码</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>

  </el-header>
</template>

<script>
// import Store from '@/store'
export default {
  data(){
    return{
      t:''
    }
  },
  computed:{
    userInfo(){
      return this.$store.state.userInfo;
    }
  },
  watch:{
    $route(to,from){
      console.log(to.path);
      console.log(from.path);
      this.t=JSON.parse(sessionStorage.getItem('store')).UserName;


    }


  },
  methods:{
    handleCommand(command) {
      if(command==='exit'){
        localStorage.removeItem('token');
        //  Store.commit('changeUserInfo','');
        sessionStorage.removeItem('store');
        sessionStorage.setItem('store',JSON.stringify({
          UserName:''
        }));
        this.$router.push({
          path:'/',

        })
      }
      else if(command==='edit'){
        this.$router.push({
          path:'/edit'
        })
      }
      else if(command==='change'){
        this.$router.push({
          path:'/change'
        })
      }
    }
  }
}
</script>

<style>
.el-header {
  color: #333;
  line-height: 100px;
  position: relative;
}
.el-dropdown{
  float: right;
}
.username{
  font-size: 20px;
  font-family:'宋体';
  float:right;
  color: #000;
  font-weight: bold;
}
.MainTitle{
  text-shadow: 0 0 2px black;
  font-size: 50px;
  font-family:'宋体';
}

.el-icon-edit{
  color: #000;
}
</style>