<template>
  <div class="login-section">
    <el-form
        label-position="top"
        :rules="rules"
        :model="ruleForm" status-icon
        ref="ruleForm" label-width="100px" class="demo-ruleForm"
    >
      <el-form-item label="用户ID" prop="id">
        <el-input type="text" v-model="ruleForm.id" autocomplete="off"></el-input>
      </el-form-item>

      <el-form-item label="用户名" prop="name">
        <el-input type="text" v-model="ruleForm.name" autocomplete="off"></el-input>
      </el-form-item>

      <el-form-item label="备注" prop="others">
        <el-input type="text" v-model="ruleForm.others" autocomplete="off"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">修改</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import { edit } from "@/service/api";
import {nowuserInfo} from "@/service/api";
import Store from '@/store'
export default {

  data() {
    return {
      ruleForm: {
        id:JSON.parse(sessionStorage.getItem("store")).UserID,
        name:JSON.parse(sessionStorage.getItem("store")).UserName,
        others:'',

      },
      rules: {
        id: [
          { required: true, message: '请输入ID', trigger: 'blur' }
        ],

        name: [
          { required: true, message: '请输入名称', trigger: 'blur' }
        ],
        others: [
          { required: true, message: '请输入备注信息', trigger: 'blur' }
        ],
      },
    };
  },
  methods: {

    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          edit({
            UserID:this.ruleForm.id,
            UserName:this.ruleForm.name,
            OtherInfo:this.ruleForm.others

          }).then((data) => {
            if(data.code === 200) {  // 成功


              this.$message({
                message: '修改成功!',
                type: 'success'
              });
              const data = nowuserInfo();
              Store.commit('changeUserInfo', data.data);
              this.$router.push({
                path:'/main/addNews',
              })
            }
          })
        } else {
          alert('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },


  }
}
</script>

<style lang="stylus">
.login-section
  padding 0px 20px
</style>
